

:root {
  --body_background-color: #F0F8FF;
  --body_color: rgb(33, 150, 150);
  --siderbar_active:  rgb(222, 221, 221);
  
  
}
[data-theme='dark'] {
  --body_background-color: #333333;
  --body_color: #58c7d3;
  --siderbar_active: #f8f8f8;
}


/* GLOBAL STYLES */
.score-section {
  color: var(--body_color);
}

.top-nav, .quiz-body {
  display: flex;
  flex-direction: column; /* Stack the items vertically */
  align-items: center; /* Center horizontally */
  text-align: center;
  padding: 10px;
  border: 3px solid var(--body_color);
  width: 600px;
  margin: 0 auto; /* Center horizontally within the page */
  background-color: var(--body_background-color);
}
.quiz-btn{
  padding-left: 10px;
}

.form-group, .form-btn, .form-group label {
  margin: 10px 0;
  color: var(--body_color);
}
#Number-of-Items{
  width: 25px;
  text-align: center;
  font-weight: bold;
}

select{
 font-weight: bold;
}

.question-count {
  padding: 10px;
  color: var(--body_color);
}
.question-text{
  padding-bottom: 10px;
  color: var(--body_color);
}
.answer-section {
  display: flex;
  flex-direction: column;
}
.answer-section button {
  padding: 10px;
  border: 3px solid var(--body_color);
 min-width: 300px;
margin-top: 10px; 
}
/* Add these styles to your CSS file */
.correct {
  background-color: #5cb85c; /* Green background for correct answers */
  color: white;
  cursor: not-allowed; /* Disable cursor for selected answers */
}

.incorrect {
  background-color: #d9534f; /* Red background for incorrect answers */
  color: white;
  cursor: not-allowed; /* Disable cursor for selected answers */
}
.answer-option{
  display: flex;
  flex-direction: column;
 
}
.answer-option span{
  padding-top: 10px;
  color:  #5cb85c;
}


@media screen and (max-width: 450px) {
  .top-nav, .quiz-body {
   width: 100%;  
  }
  .form-group{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  #Number-of-Items{
    width: 25px;
    text-align: center;
  }

  .form-btn{

  }
}
